  margin: 20px auto;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 30px;
}

#search-box {
  font: inherit;
  max-width: 600px;
  border-bottom: 2px solid #212121;
  border-radius: 10px;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px;
  font-size: 20px;
  display: block;
