  margin-bottom: 3px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 5px;
  display: flex;
}

.country-list li:hover {
  color: rgba(255 248 220, 1);
  background-color: tan;
  background-image: linear-gradient( rgba(210 180 140, .1) 30%, rgba(255 127 80, .1) 20% );
